import {
  BOOKS_REQUEST,
  BOOKS_REQUEST_COMPLETED,
  BOOKS_REQUEST_ERROR,
} from '../constants';

export const bookRequest = (params) => ({
  type: BOOKS_REQUEST,
  payload: params,
});

export const bookRequestCompleted = (params) => ({
  type: BOOKS_REQUEST_COMPLETED,
  payload: params,
});

export const bookRequestError = (err) => ({
  type: BOOKS_REQUEST_ERROR,
  payload: err,
});




