import {
  SORT_CHANGE,
} from '../constants';

const validSorts = {
  date: 'date',
  author: 'author',
  book: 'book',
};

const defualtState = validSorts.date;

export default function (state, action) {
  const actions = {
    [SORT_CHANGE]: () => (validSorts[action.payload.sort] || defualtState),
  };

  return actions[action.type] ? actions[action.type]() : defualtState;
}



