import {
  BOOKS_REQUEST_COMPLETED,
} from '../constants';

const defaultState = {
  stack: [],
  skip: 0,
  last: 0,
};

export default function (state, action) {
  const actions = {
    [BOOKS_REQUEST_COMPLETED]: () => {
      // append new books
      if (action.payload.skip > state.last) {
        return {
          ...state,
          stack: [
            ...state.stack,
            ...action.payload.stack,
          ],
          last: state.last + action.payload.stack.length,
        };
      }
      // prepend new books
      return {
        ...state,
        stack: [
          ...action.payload.stack,
          ...state.stack,
        ],
        skip: action.payload.skip,
      };
    },
  };

  return actions[action.type] ? actions[action.type]() : defaultState;
}



