import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styles from './BookList.css';

/**
 * Describes the Schedule List react component
 */
class BookList extends Component {
  constructor(props) {
    super(props);
    this.batchSize = 20;
  }
  componentWillMount() {
    this.props.bookRequest({
      skip: 0,
      limit: this.batchSize,
      filters: this.props.filters,
      sort: this.props.sort,
      order: this.props.order,
    });
  }

  componentDidMount() {
    this.scrollerElement.addEventListener('scroll', this.scrollHandler);
  }

  getFormatedDate = (book) => {
    const d = new Date(book.publishDate);
    return `${d.getDate()}/${(d.getMonth() + 1)}/${d.getFullYear()}`;
  }

  scrollHandler = (e) => {
    const node = e.srcElement;
    const scrollPer = (node.scrollTop / (node.scrollHeight - node.offsetHeight)) * 100;
    console.log(scrollPer);
    if (scrollPer > 70) {
      this.props.bookRequest({
        skip: this.props.books.skip + this.batchSize,
        limit: this.batchSize,
        filters: this.props.filters,
        sort: this.props.sort,
        order: this.props.order,
      });
    }
  }

  captureScroller = (node) => {
    this.scrollerElement = node;
  }

  render() {
    return (<div>
      <h2>Boooks</h2>
      <div ref={this.captureScroller} className={styles.bookListContainer}>
        {
          this.props.books.stack.map((book) => (
            // eslint-disable-next-line
            <div className={styles.bookRow} key={book._id}>
              <div><b>{book.name}</b></div>
              <div>In: {book.genre.name}</div>
              <div>By: {book.author.name} , ({book.author.gender})</div>
              <div>On: {this.getFormatedDate(book)}</div>
            </div>
          ))
        }
      </div>
    </div>);
  }
}

BookList.propTypes = {
  books: PropTypes.array,
  bookRequest: PropTypes.func,
  filters: PropTypes.object,
  sort: PropTypes.string,
  order: PropTypes.string,
};

export default BookList;



