import {
  FILTER_CHANGE,
} from '../constants';

import genres from '../../book-generator/genres';

const defualtState = {
  gender: {
    enabled: false,
    value: 'FEMALE',
    query: 'gender',
  },
  genre: {
    enabled: false,
    value: genres.indeterminate,
    query: 'genre',
  },
};

export default function (state, action) {
  const actions = {
    [FILTER_CHANGE]: () => {
      const newState = JSON.parse(JSON.stringify((state)));
      const newFilter = action.payload.filter;

      newState[newFilter.name] = {
        ...newState[newFilter.name],
        enabled: newFilter.enabled,
        value: newFilter.value,
      };

      return newState;
    },
  };

  return actions[action.type] ? actions[action.type]() : defualtState;
}



