import {
  ORDER_CHANGE,
} from '../constants';

const validOrders = {
  asc: 'asc',
  dsc: 'dsc',
};

const defualtState = validOrders.asc;

export default function (state, action) {
  const actions = {
    [ORDER_CHANGE]: () => (validOrders[action.payload.order] || defualtState),
  };

  return actions[action.type] ? actions[action.type]() : defualtState;
}



